:root {
  /* Colors */
  --color-main: #0494cb;
  --color-second: #0494cb;
  --color-neutral: #abacaf;
  --color-bg-gray: rgb(238, 238, 238);
  --color-alert: rgb(235, 51, 51);

  /* Fonts */
  --font-title: "Asap Condensed", sans-serif;
  --font-body: "Raleway", sans-serif;

  /* Font Sizes */
  --root-size: 16px;
  --h1-size: 3.2em;
  --h2-size: 1.8em;
  --h3-size: 1.3em;
  --h4-size: 1.1em;
  --subtitle-size: 0.8em;
  --tiny-size: 0.6em;
}

html,
body,
h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

html {
  font-size: var(--root-size);
  font-family: var(--font-body);
  background-color: #ececec;
}

body {
  font-family: var(--font-body);
}

h1 {
  font-family: var(--font-title);
  font-size: var(--h1-size);
}

h2 {
  font-family: var(--font-body);
  font-size: var(--h2-size);
  font-weight: 900;
}

a {
  color: var(--color-main);
}

.result>.error-detail>ul>li {
  padding: 2px;
}

.result>.error-detail>ul>li:hover {
  background-color: rgb(238, 238, 238);
}

.list {
  column-count: 2;
  margin: 15px 0px;
}

.result-list {
  column-count: 2;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  padding: 10px;
  margin: 20px 0px;
  width: 50%;
  font-family: var(--font-body);
  font-size: var(--h3-size);
  font-weight: 900;
}


/* GRID */
.columns {
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 15px;
}

.column1,.row1,.row2 {
  margin: 0px 0px 15px 0px;
}

.form-columns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
}

.loading-screen {
  background-color: var(--color-bg-gray);
  opacity: 0.85;
  position: absolute;
  width: 100%;
}

.loading-screen-text {
  position: sticky;
  top: 25%;
  left: 45%;
  transform: translate(-50%, -50%);
  font-size: var(--h2-size);
  font-weight: 900;
}



/* FORM FIELDS */

.version-spacer {
  display: absolute;
  margin: 0;
  padding: 0;
}

.version-number {
  margin: 0px 5px;
  font-size: var(--subtitle-size);
  padding: 0;
  color: gray;
  font-family: var(--font-title);
}

.version-text {
  margin: 4px 15px;
}

.debug-label {
  display: grid;
  grid-template-columns: 1fr 3fr;
}

.debug-label>input {
  margin: 4px;
  padding: 0;
}

.debug-label>label {
  margin: 4px 4px 4px 0px;
}

input {
  font-family: var(--font-body);
  font-size: 1rem;
  margin: 5px 0px;
  padding: 10px;
  width: 95%;
  font-weight: 500;
  border: 2px solid var(--color-bg-gray);
  background-color: var(--color-bg-gray);
}

select {
  font-family: var(--font-body);
  font-size: 1rem;
  margin: 5px 0px;
  padding: 10px;
  width: 100%;
  font-weight: 500;
  border: 2px solid var(--color-bg-gray);
  background-color: var(--color-bg-gray);
}

input:focus,select:focus {
  outline: none !important;
  border: 2px solid var(--color-main);
}

input:hover,select:hover {
  outline: none !important;
  border: 2px solid gray;
}

#filePicker {
  font-weight: 500;
  font-family: var(--font-body);
}

.question-text {
  font-family: var(--font-title);
  font-size: 20px;
}

.form-field {
  padding: 10px;
  background-color: white;
  font-weight: 600;
}

.month-year-input {
  display: grid;
  grid-template-columns: 1.6fr 1fr;
  column-gap: 5px;
}


/* STATUS */

.success {
  color: var(--color-second);
}

.fail {
  color: var(--color-alert);
}

.neutral {
  color: grey;
}

.inactive {
  background-color: var(--color-neutral);
  color: white;
  border: 3px solid var(--color-neutral);
}

.active {
  background-color: var(--color-main);
  color: white;
  border: 3px solid var(--color-main);
  font-size: var(--h3-size);
}

.active:hover {
  background-color: white;
  color: var(--color-main);
  border: 3px solid var(--color-main);
  cursor: pointer;
}

.waiting {
  margin: 15px;
  padding: 15px;
  background-color: white;
  color: rgb(77, 77, 77);
  font-size: var(--h4-size);
  border: none;
}

.waiting:hover {
  background-color: lightgrey;
  cursor: pointer;
}

.hide {
  position: absolute !important;
  top: -9999px !important;
  left: -9999px !important;
}


/* NAVBAR */
.navbar-wrapper {
  position: sticky;
  top: 0px;
}
.navbar {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr 2fr 1fr;
  column-gap: 2%;
  width: 100%;
  background-color: var(--color-bg-gray);
}

.navbar-button {
  width: 100%;
  border: none;
  background-color: var(--color-bg-gray);
  font-family: var(--font-title);
  margin: 0%;
  color: gray;
  padding: 15px;
  font-size: var(--h3-size);
  font-weight: 900;
}

.navbar-active {
  background-color: var(--color-main);
  color: white;
}

.navbar-active:hover {
  background-color: var(--color-main);
  color: white;
}

/* VALIDATION STEPS */

.status {
  border: 1px solid grey;
  margin: 1%;
  padding: 5%;
}

.step-header {
  padding: 20px;
  margin: 0%;
  line-height: 200%;
  background-color: var(--color-main);
  color: white;
}

.step-content {
  padding: 20px 35px;
  margin: 0px;
  background-color: white;
}

.validation-step {
  display: grid;
  grid-template-columns: 7fr 1fr;
  border-bottom: 1px solid lightgrey;
  font-family: var(--font-title);
}

.validation-name:hover {
  cursor: pointer;
}

.validation-name {
  font-size: var(--h3-size);
  font-weight: 500;
  margin: 0%;
  padding: 15px 10px;
}

.validation-info {
  margin: 5px 0px 10px 0px;
  padding: 1% 2% 2% 2%;
  max-height: 400px;
  overflow: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
}

.validation-symbol {
  font-size: var(--h3-size);
  font-weight: 900;
}

.toggle {
  font-size: var(--tiny-size);
  padding: 10px 0px 0px 20px;
  color: rgb(90, 89, 89);
  font-weight: 600;
  font-family: var(--font-body);
}

.info {
  background-color: rgb(231, 231, 231);
  color: rgb(51, 51, 51);
  padding: 10px;
  margin: 0px 0px 10px 0px;
}

.result {
  padding: 10px;
  margin: 0px 0px 10px 0px;
  border: 2px solid rgb(231, 231, 231);
}

.info>h3,.result>h3 {
  margin: 0px 0px 5px 0px;
}

.helptext {
  margin: 5px;
  font-size: var(--subtitle-size);
  font-weight: 600;
  color: var(--color-alert);
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
}

.asterisk {
  color: var(--color-alert);
  font-size: var(--h4-size);
  font-weight: 900;
}


/* NO LONGER MEETS POP CRITERIA */
.popChange {
  margin: 0% 5% 5% 5%;
}

.popChange-title {
  text-align: left;
  margin: 2%;
  font-size: var(--h3-size);
  font-family: var(--font-title);
}

.popChange-input {
  display: grid;
  grid-template-columns: 1fr;
  border: 5px solid white;
  width: 100%;
  max-width: 150px;
}

.popChange-label {
  font-size: var(--subtitle-size);
  font-family: var(--font-body);
  font-weight: 600;
}

.popChange-form {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 1%;
}


/* AGGREGATION */

.filter-buttons {
  line-height: 25px;
  margin: 0% 5%;
}

.reporting-month {
  margin: 15px 0px 5px 0px;
  font-size: var(--h3-size);
  font-weight: 600;
  color: var(--color-main);
}

.reporting-community {
  margin: 5px 0px 15px 0px;
  font-size: var(--root-size);
}

.agg-table {
  display: grid;
  grid-template-columns: 3.5fr 1fr 3.5fr 1fr;
  column-gap: 0px;
  font-family: var(--font-title);
  margin: 0% 5%;
}

.agg-header {
  font-size: var(--h4-size);
  font-weight: 800;
  margin: 0px 0px;
  padding: 6px;
  background-color: lightgrey;
  font-family: var(--font-body);
}

.agg-value {
  font-size: 20px;
  margin: 0px 20px 0px 0px;
  padding: 4px;
  border-bottom: 2px solid var(--color-bg-gray);
}

.agg-value-calc {
  font-size: var(--root-size);
  margin: 0px;
  padding: 8px;
  border-bottom: 2px solid var(--color-bg-gray);
  font-weight: 900;
}

.agg-value-calc:hover, .agg-value:hover {
  cursor: pointer;
}

.metric-tooltip {
  position: absolute;
  padding: 5px;
  background-color: white;
  margin: 15px 0px 0px;
  border: 2px solid lightblue;
  opacity: 0.95;
  max-width: 300px;
}

.metric-help-text {
  margin: 0;
  font-size: 14px;
  color:rgb(31, 30, 30)
}

.agg-spacer {
  font-size: var(--root-size);
  margin: 0px 0px;
  padding: 6px;
}

.button-group {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  column-gap: 0px;
}

.button-group > button {
  padding: 10px;
  margin: 20px 5px;
  width: 90%;
  font-size: var(--root-size);
  font-weight: 600;
  background-color: lightblue;
  color: var(--color-main);
  border: 5px solid white;
  font-family: var(--font-title);
}

.button-group > button:hover {
  background-color: var(--color-main);
  color: white;
  border: 5px solid var(--color-main);
  cursor: pointer;
}

.button-group > button:focus {
  background-color: var(--color-main);
  color: white;
  border: 5px solid var(--color-main);
  cursor: pointer;
}

.button-group-title {
  font-size: var(--h3-size);
  font-weight: 900;
  text-align: center;
  background-color:var(--color-bg-gray);
  color:black;
  padding: 10px;
  margin: 10px;
}

.button-group-title>b{
  font-weight: 900;
}

.button-group-subtitle {
  font-size: var(--h3-size);
  margin: 0px 0px 10px;
  font-weight: 600;
}

.button-group-instructions {
  color:var(--color-neutral);
}

.reupload-submit,
.reupload-aggregate,
.reupload-popCriteria,
.new-upload-submit,
.download-btn {
  background-color: white;
  color: gray;
  border: 0px solid var(--color-main);
  font-size: var(--root-size);
  margin: 15px 0px 10px;
  padding: 10px;
  width: 100%;
}

.reupload-submit:hover,
.reupload-aggregate:hover,
.reupload-popCriteria:hover,
.new-upload-submit:hover,
.download-btn:hover {
  color: var(--color-main);
  cursor: pointer;
}


.submit-instructions {
  margin: 0px 20px 0px 20px;
  font-weight: 600;
  text-align: center;
}

.agg-progress-msg,.submit-progress-msg {
  font-size: var(--h3-size);
  font-weight: 600;
  margin: 10px 20px 0px 20px;
}

.aggregateBtn-msg {
  display: grid;
  grid-template-columns: 1fr;
  margin: 25px 0px 0px 0px;
}

.submitBtn-msg {
  display: grid;
  grid-template-columns: 1fr;
  margin: 25px 0px 0px 0px;
}

.review-msg {
  margin: 25px 10px 0px 10px;
  padding: 10px;
}

.popStatus-instructions {
  display: grid;
  grid-template-columns: 1fr 7fr;
  column-gap: 0px;
  row-gap: 0px;
  margin: 10px;
}

.popStatus-values {
  font-size: var(--root-size);
  margin: 0px 0px;
  padding: 10px;
  border-bottom: 1px solid lightgrey;
}

.popStatus-values:hover {
  background-color: rgb(235, 235, 235);
}


/* Inner container */
progress[value] {
  /* Reset the default appearance */
  -webkit-appearance: none;
  appearance: none;
  width: 80%;
  height: 20px;
  margin: 20px;
  border: 3px solid lightgray;
  border-radius: 10px;
}

/* Outer Container */
progress[value]::-webkit-progress-bar {
  border-radius: 10px;
}

/* Progress bar */
progress[value]::-webkit-progress-value {
  background-color: var(--color-main);
  border-radius: 10px;
}



/* MEDIA BREAKPOINTS */

/* Smartphones (portrait) ----------- */
@media only screen and (max-width: 800px) {
  button {
    width: 75%;
  }

  .columns {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 10px;
    margin: 5px;
  }

  .form-columns {
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 60px;
  }

  .version-number {
    grid-template-columns: 2.5fr 1fr;
    margin: 0px;
  }

  .version-text {
    margin: 0px 20px 0px 0px;
  }

  .debug-label {
    margin: 0;
    grid-template-columns: 1fr 5fr;
  }

  .debug-label>input {
    margin: 0px;
  }

  .debug-label>label {
    margin: 0px 0px 0px 3px;
  }

  .filter-buttons {
    margin: 0%;
  }
  
  .agg-table {
    margin: 0%;
  }

  .step-content {
    padding: 10px 15px;;
  }

  .submission-form {
    margin: 0px;
  }

  input {
    max-width: 93%;
  }

  .validation-info {
    grid-template-columns: 1fr;
  }

  .validation-step {
    grid-template-columns: 5fr 1fr;
  }

  .button-group {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 10px;
    margin: 10px 10px 30px 10px;
  }

  .button-group > button {
    padding: 10px;
    margin: 10px 0px 0px;
    width: 100%;
  }
}
